<template>
  <div id="app">
    <el-card class="componentCard">
      <h1>番茄闹钟</h1>
      <div class="display">
        {{ nowTime }}
      </div>
      <div class="settingTime">
        <el-button round @click="sub">-</el-button>
        <span>{{ changeTime }}</span>
        <el-button round @click="add">+</el-button>
      </div>
      <div class="operate">
        <el-button round @click="startCountdown">开始</el-button>
        <el-button round @click="pauseCountdown">暂停</el-button>
        <el-button round @click="removeCountdown">终止</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      lastTime: 0,
      setMin: 25,
      setHours: 0,
      flag: true,
      Timer: '',
      changeTime: '00:25:00'
    }
  },
  computed: {
    nowTime() {
      let H = Math.floor(this.lastTime / 3600)
      let M = Math.floor(this.lastTime / 60) % 60
      let S = this.lastTime % 60
      if (H < 10) H = `0${H}`
      if (M < 10) M = `0${M}`
      if (S < 10) S = `0${S}`
      return `${H}:${M}:${S}`
    }
  },
  methods: {
    setTime() {
      if (this.flag === true) {
        this.lastTime = this.setHours * 3600 + this.setMin * 60
        this.flag = false
      }
      // console.log(this.lastTime / 3600)
      // console.log(Math.floor(this.lastTime / 3600))

      if (this.lastTime > 0) {
        this.lastTime--
      } else {
        clearInterval(this.Timer)
        return alert('倒计时结束')
      }
    },
    pauseCountdown() {
      clearInterval(this.Timer)
      alert('倒计时暂停')
    },
    startCountdown() {
      this.Timer = setInterval(() => {
        this.setTime()
      }, 1000)
    },
    removeCountdown() {
      this.lastTime = 0
      this.flag = true
      clearInterval(this.Timer)
    },
    sub() {
      this.setMin--
      if (this.setMin >= 60) {
        this.setHours = Math.floor(this.setMin / 60)
        // console.log(this.setHours)
        this.setMin = this.setMin - this.setHours * 60
        if (this.setHours < 10) this.setHours = `0${this.setHours}`
        if (this.setMin < 10) this.setMin = `0${this.setMin}`
        this.changeTime = `${this.setHours}:${this.setMin}:00`
      } else if (this.setHours * 60 + this.setMin < 10) {
        this.setMin = 10
        this.changeTime = `00:10:00`
        alert('不到十分钟了,还减还减')
      } else if (this.setMin < 10 && this.setHours * 60 + this.setMin > 10) {
        this.changeTime = `${this.setHours}:0${this.setMin}:00`
      } else {
        this.changeTime = `${this.setHours}:${this.setMin}:00`
      }
    },
    add() {
      this.setMin++
      if (this.setMin >= 60) {
        this.setHours = Math.floor(this.setMin / 60)
        this.setMin = this.setMin - this.setHours * 60
        if (this.setHours < 10) this.setHours = `0${this.setHours}`
        if (this.setMin < 10) this.setMin = `0${this.setMin}`
        this.changeTime = `${this.setHours}:${this.setMin}:00`
      } else if (this.setMin < 10) {
        this.changeTime = `${this.setHours}:0${this.setMin}:00`
      } else {
        this.changeTime = `${this.setHours}:${this.setMin}:00`
      }
    }
  }
}
</script>

